import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import profiles from "src/static/resume.json";
import i18n from "src/i18n";
import { request } from "src/service/request";

const jsonUri = (window as any).__URL__ || null;
const lang = (i18n.language || 'vi') as string;

const fetchApi = () => {
    return jsonUri ? request(jsonUri) : Promise.reject()
}

export interface appState {
    cv: any,
    profiles: any,
    language: string,
    print: boolean,
    page: number
}

const initialState: appState = {
    cv: (profiles as any)[lang],
    profiles: profiles,
    language: lang,
    print: false,
    page: 1
}

export const appSlice = createSlice({
    name: 'app',
    initialState,
    reducers: {
        doPrint: (state, action: PayloadAction<boolean>) => {
            state.print = action.payload
            if (action.payload) {
                setTimeout(() => (window as any).print(), 400)
            }
        },
        setLanguage: (state, action: PayloadAction<string>) => {
            state.language = action.payload
            state.cv = state.profiles[action.payload] || {}
        },
        setPage: (state, action: PayloadAction<number>) => {
            state.page = action.payload
        },
        fetch: (state) => {
            fetchApi().then((response: any) => state.profiles = response).catch(() => { })
        }
    },
})

// Action creators are generated for each case reducer function
export const { doPrint, setLanguage, fetch, setPage } = appSlice.actions

export default appSlice.reducer