import React, { useEffect, useMemo } from "react";
import { useDispatch } from 'react-redux'
import { fetch } from 'src/store/slice'

const Api: React.FC = () => {
    const dispatch = useDispatch()
    const fetchApi = () => dispatch(fetch())
    // mount
    useEffect(() => {
        fetchApi()
    });

    return useMemo( () => (
        <></>
    ), []);
};

export default Api;
