import React from "react";
import "src/app/style.css";
import { useSelector, useDispatch } from 'react-redux'
import { doPrint } from 'src/store/slice'

import Content from "src/components/Content";
import Print from "src/components/Print";
import Toolbar from "src/components/Toolbar";

const App: React.FC = () => {
    const dispatch = useDispatch()
    const print = useSelector((state: any) :boolean => state.app.print );

    (window as any).onafterprint = () => {
        dispatch(doPrint(false))
    };

    return (
        <div className="vc--resume" role="application">
            <div className="row">
                <div className="vc--resume col-12">
                    {print ? (
                        <Print />
                    ) : (
                        <Content />
                    )}
                </div>
                <Toolbar />
            </div>
        </div>
    );
};

export default App;
